.footer {
  background-color: #041827;
  color: rgba(255, 255, 255, 0.7);
}

.footer__wrapper {
  margin: 0 auto;
  max-width: 1220px;
  padding: 50px 0 44px;

  @include vp-1023 {
    padding: 50px 26px 54px 12px;
  }
}

.footer__partitions {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 500px 1fr;

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 16px;
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
  }
}

.footer__partittion {
  @include vp-1023 {
    grid-column: 1 / -1;
  }
}
